<template>
  <div class="vehicle-item" @click="clickCard">
    <div class="img">
      <img :src="verify.mall_imgurl || '/static/img/hongyanbg.jpg'" alt="">
    </div>
    <div class="infos">
      <h5>{{verify.name}}</h5>
      <p>使用码: {{verify.token}}</p>
      <p>使用时间：{{verify.create_time}}</p>
      <p>状态：{{verify.end_handle}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Dealer-Card-Item',
  props: {
    verify: Object
  },
  methods: {
    clickCard () {
      this.$parent.$emit('clickOneVehicleCard', this.verify)
    }
  }
}
</script>

<style lang="less" scoped>
  .vehicle-item{
    display: flex;
    padding: .6rem;
    margin-top: .6rem;
    align-items: center;
    background-color: #fff;
    box-shadow: 0 1px 11px 1px #888;
    .img{
      width: 3rem;
      img{
        width: 100%;
      }
    }
    .infos{
      flex: 1;
      padding-left: 1rem;
      h5{
        margin: 0;
      }
      p{
        margin: 0;
        font-size: 12px;
        color: #999;
      }
    }
  }
</style>



