<template>
  <div class="detail">
    <i class="fa fa-times-circle" aria-hidden="true" @click="closeModal" style="font-size: 2rem; color: red;position: absolute;right: 2rem;"></i>
    <div class="sih" v-if="dealerInfo.role === 3 || dealerInfo.role === 4">
        <span>名称：{{verifyDetail.name}}</span>
        <span>使用码：{{verifyDetail.token}}</span>
        <span>抵扣金额：{{verifyDetail.price}}</span>
        <span>Vin：{{verifyDetail.vin8}}</span>
        <span>开始经销商名称：{{verifyDetail.start_cust_name}}</span>
        <span v-if="verifyDetail.start_handle_time">开始核销时间：{{verifyDetail.start_handle_time}}</span>
        <span>开始经销商状态：{{verifyDetail.start_handle}}</span>
        <span>结束经销商名称: {{verifyDetail.end_cust_name}}</span>
        <span>结束经销商状态：{{verifyDetail.end_handle}}</span>
        <span v-if="verifyDetail.end_handle_time">结束核销时间：{{verifyDetail.end_handle_time}}</span>
        <span>SIH状态：{{verifyDetail.end_handle}}</span>
        <img :src="verifyDetail.imgurl || '/static/img/default-bg.jpg'" alt="" style="width:100%; height: 155px;">
        <mt-button v-show="verifyDetail.allow" size="large" type="primary" @click="verifyCard" style="margin: 0 auto;">核销该卡券</mt-button>
    </div>
    <div v-else>
        <span>名称：{{verifyDetail.name}}</span>
        <span>使用码：{{verifyDetail.token}}</span>
        <span>抵扣金额：{{verifyDetail.price}}</span>
        <span>使用时间：{{verifyDetail.create_time}}</span>
        <span>核销时间：{{verifyDetail.handle_time}}</span>
         <img :src="verifyDetail.imgurl || '/static/img/default-bg.jpg'" alt="" style="width:100%; height: 155px;">
    </div>
    </div>
</template>

<script>
import bus from '../../assets/js/bus'
export default {
  name: 'Dealer-Verify-Detail',
  props: {
    verifyDetail: '',
    dealerInfo: Object
  },
  destroyed () {
    bus.$off('clickOneVerifyCard')
  },
  methods: {
    verifyCard () {
      bus.$emit('clickOneVerifyCard', this.verifyDetail)
    },
    closeModal () {
      bus.$emit('closeModalbox')
    }
  }
}
</script>

<style lang="less" scoped>
  .detail{
    padding: 1rem;
    span{
      padding: .5rem .5rem;
      display: block;
      font-size: 14px;
      &:nth-child(2n) {
        background-color: #eee;
      }
    }
  }
</style>



