<template>
  <div class="list-wrap">
      <app-header title="核销列表" showBack="true"></app-header>
      <div class="list-main">
        <div style="display: flex;" v-show="dealerInfo.role === 3">
          <input type="text" v-model="keywords" placeholder="请输入经销商编号" style="flex: 1; padding: .5rem;">
          <mt-button size="small" @click="search" type="primary">搜索</mt-button>
        </div>
        <div class="ui-title">
          <router-link to="/dealer/download" style="color: #333;">报表下载</router-link>
        </div>
        <div v-if="verifyList.length">
          <dealer-card-item
            v-for="(item, index) in verifyList"
            :key="index"
            :verify="item"
          />
        </div>
        <div v-else>
          <h3 style="text-align: center;">暂无卡券</h3>
        </div>
      </div>
      <mt-popup
        style="width: 100%; height: 100%;"
        v-model="popupVisible"
        pop-transition="popup-fade">
        <dealer-verify-detail :verifyDetail="verifyDetail" :dealerInfo="dealerInfo"></dealer-verify-detail>
    </mt-popup>
  </div>
</template>

<script>
// 编号，名称，使用码，时间，状态
import AppHeader from '../../components/header/header'
import DealerCardItem from './dealer-card-item'
import DealerVerifyDetail from './dealer-verify-detail'
import {getVerificationList, dealerVerificationSih} from '../../api/maintain'
import bus from '../../assets/js/bus'
export default {
  name: 'Dealer-Verify-List',
  data () {
    return {
      verifyList: [],
      verifyDetail: '',
      popupVisible: false,
      dealerInfo: '',
      keywords: ''
    }
  },
  created () {
    this.getDatas()
    if (typeof this.$store.getters.getDealerInfo === 'string') {
      this.dealerInfo = JSON.parse(this.$store.getters.getDealerInfo)
    } else {
      this.dealerInfo = this.$store.getters.getDealerInfo
    }
  },
  mounted () {
    this.$on('clickOneVehicleCard', vehicle => {
      this.popupVisible = true
      this.verifyDetail = vehicle
    })

    bus.$on('clickOneVerifyCard', (card) => {
      dealerVerificationSih(card.id)
        .then(data => {
          if (data.state) {
            this.$toast(data.errormsg || '核销成功')
            this.popupVisible = false
            this.getDatas()
          } else {
            this.$toast(data.errormsg || '核销失败')
          }
        })
        .catch(err => {
          console.log(err)
          this.$toast('网络错误，请重试')
        })
    })

    bus.$on('clickCloseModal', () => {
      this.showTimeState = false
    })
    bus.$on('closeModalbox', () => {
      this.popupVisible = false
    })
  },
  methods: {
    getDatas () {
      getVerificationList()
        .then(data => {
          if (data.state) {
            this.verifyList = data.data.list
          } else {
            this.$toast(data.errormsg || '网络错误')
          }
        })
        .catch(err => {
          console.log(err)
          this.$toast('网络错误，请重试')
        })
    },
    search () {
      getVerificationList(false, this.keywords)
        .then(data => {
          if (data.state) {
            this.verifyList = data.data.list
          } else {
            this.$toast(data.errormsg || '网络错误')
          }
        })
        .catch(err => {
          console.log(err)
          this.$toast('网络错误，请重试')
        })
    }
  },
  components: {AppHeader, DealerCardItem, DealerVerifyDetail}
}
</script>

<style lang="less" scoped>
.list-wrap{
  background: #e5e5e5;
  height: 100%;
  padding: 55px 0;
  overflow-x: hidden;
  overflow-y: scroll;
}
</style>



